// В этом файле должны быть стили для БЭМ-блока gallery, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.gallery {

  $block-name:                &; // #{$block-name}__element

  padding: 40px 0;
  padding-bottom: 0;
  overflow: hidden;

  @include md {
    padding: 70px 0;
  }
  @include xl {
    padding: 120px 0;
  }

  .gallery__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding: 0;

    @include xl {
      align-items: flex-start;
      flex-wrap: nowrap;
    }
  }
}

.gallery__title {
  padding-left: 15px;
  font-size: 36px;
  flex-shrink: 0;
}

.gallery__slider {
  display: none;
  flex-grow: 1;
  overflow: hidden;
  margin-top: 10px;

  @include sm {
    display: block;
    margin-top: 20px;
  }

  &--mob {
    display: block;

    @include sm {
      display: none;
    }

    .slick-slide {
      margin: 0 5px;
    
      @include sm {
        margin: 0;
      }
    }
  }

  .slick-track {
    
    @include sm {
      display: flex;
      align-items: center;
    }
  }
  .slick-slide {

    &:hover {
      z-index: 2;
    }
  }
}

.gallery__item {

}

.gallery__pic {
  position: relative;
  transition: all .4s ease;

  &:hover {
    z-index: 2;
    transform: scale(1.1);
  }
  
  img {
    width: 100%;
  }
}

.gallery__nav {
  flex-basis: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
  padding: 5px 15px;
  background: $color-main;
  flex-shrink: 0;

  @include sm {
    flex-basis: auto;
    margin: 0;
  }
  @include xl {
    order: 2;
  }
}

.gallery__arrows {
  display: none;

  @include sm {
    display: flex;
  }

  .slick-arrow {
    position: static;
    width: 50px;
    height: 30px;
    transform: none;

    svg {
      width: 29px;
      height: auto;
      position: relative;
      font-size: 16px;
      fill: $black;
    }
  }
}

.gallery__arrows-mob {
  display: flex;
  margin-right: 20px;

  @include sm {
    display: none;
  }

  .slick-arrow {
    position: static;
    width: 50px;
    height: 30px;
    transform: none;

    svg {
      width: 29px;
      height: auto;
      position: relative;
      font-size: 16px;
      fill: $black;
    }
  }
}

.gallery__count {
  font-family: $font-title;
  font-size: 24px;

  @include sm {
    display: none;
  }
}