// В этом файле должны быть стили для БЭМ-блока faq, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.faq {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  padding: 40px 0;
  background: $color-secondary;
  overflow: hidden;

  @include md {
    padding: 60px 0;
  }
  @include xl {
    padding: 90px 0;
  }

  &:after {

    @include xl {
      content: '';
      display: block;
      width: 280px;
      height: 280px;
      border-radius: 50%;
      background: url('../img/faq.jpg') no-repeat;
      background-size: cover;
      position: absolute;
      bottom: -100px;
      right: 0;
    }

    @include rwd(1900) {
      right: 150px;
    }
  }

  .container {
    padding: 0;

    @include sm {
      padding: 0 15px;
    }
  }
}

.faq__title {
  margin-bottom: 25px;
  font-size: 28px;
  color: $white;
  padding: 0 15px;

  @include sm {
    padding: 0;
    font-size: 36px;
  }
}

.faq__list {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 1px;
  gap: 1px;
  background: $white;
  padding: 1px 0;

  @include md {
    grid-template-columns: 1fr 1fr;
  }
}

.faq__item {
  position: relative;
  // padding: 30px 5px;
  // padding-top: 35px;
  color: $white;
  background: $color-secondary;
  overflow: hidden;

  @include md {
    // padding: 30px;
    // padding-top: 35px;
    justify-content: flex-start;
  }

  &:hover {

    @include xl {

      .faq__answer {
        transform: translateY(0);
      }
    }
  }

  &--show {
   
    .faq__question:before {
      transform: translateY(-50%) rotate(180deg);
    }
  }

}

.faq__question {
  position: relative;
  padding: 35px 25px;
  
  @include xl {
    position: absolute;
    top: 20px;
    left: 20px;
    right: 20px;
  }

  &:before {
    content: '';
    display: block;
    width: 16px;
    height: 10px;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    background-image: url("data:image/svg+xml,%3Csvg width='16' height='10' viewBox='0 0 16 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14 2C11.2663 4.73367 8 8 8 8L2 2' stroke='%23ffffff' stroke-width='3.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-size: contain;
    transition: all 0.2s;
  }  
}

.faq__answer {
  position: relative;
  display: none;
  padding: 20px;
  background: $black;
  color: $white;
  font-size: 16px;
  z-index: 2;

  @include xl {
    display: block !important;
    transform: translateY(-100%);
    transition: all .4s;
    z-index: 4;
  }

  p {
    color: $white;
  }
}