// В этом файле должны быть стили для БЭМ-блока adaptation-slider, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.adaptation-slider {

  $block-name:                &; // #{$block-name}__element

  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  @include md {
    display: none;
    opacity: 0;
  }

  div {
    height: 100%;
  }
}

.adaptation-slider__item {
  height: 100%;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}


.adaptation-slider-nav {
  padding-bottom: 30px;
  overflow: hidden;

  @include sm {
    display: none;
    opacity: 0;
  }

  .slick-arrow {
    width: 50px;
    height: 50px;

    svg {
      width: 2em;
      height: 2em;
      position: relative;
      font-size: 16px;
      fill: $white;
    }
  }

  .slick-prev {
    top: 26px;
    left: 5px;
  }
  
  .slick-next {
    top: 26px;
    right: 5px;
  }
}

.adaptation-slider-nav__list {
  padding-top: 12px;

  &::before {
    content: '';
    display: block;
    width: 100%;
    height: 50px;
    border: 1px solid $white;
    position: absolute;
    top: 0;
    left: 0;
  }
}

.adaptation-slider-nav__item {
  min-height: 250px;
  color: $white;
  font-size: 20px;
  font-weight: 500;
  text-align: center;
}