// В этом файле должны быть стили для БЭМ-блока page-header, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.75);
  z-index: 9990;
  transition: all .4s;

  @include sm {
    position: absolute;
    background: none;
  }

  .container {
    display: flex;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #fff;

    @include lg {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }

  &--fixed {

    @include sm {
      position: fixed;
      background: rgba(0, 0, 0, 0.75);
      animation: slideInDown .4s both;
    }

    .page-header__inner {
      max-width: 100%;
    }
  }
}

