// В этом файле должны быть стили для БЭМ-блока deal, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.deal {

  $block-name:                &; // #{$block-name}__element

  padding-bottom: 30px;
  overflow: hidden;

  @include md {
    padding: 0;
  }
}

.deal__title {
  margin-bottom: 30px;
  padding: 0 15px;
  font-size: 36px;
  text-align: center;

  @include xl {
    margin-bottom: 60px;
  }
}

.deal__list {

  @include md {
    border-top: 1px solid $black;
  }

  .container {
    padding: 0;

    @include md {
      padding: 0 15px;
      padding-top: 40px;
      display: flex;
      justify-content: space-between;
    }
    @include xl {
      padding-top: 90px;
    }
  }
}




