// В этом файле должны быть стили для БЭМ-блока about-feature, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.about-feature {

  $block-name:                &; // #{$block-name}__element

  max-width: 430px;
  margin: auto;
  margin-bottom: 50px;

  @include sm {
    display: flex;
    flex-wrap: wrap;
    max-width: 800px;
  }

  @include lg {
    margin-bottom: 80px;
  }
}

.about-feature__item {

  @include sm {
    width: 50%;
  }

  &:last-child {
    order: 3;
    margin-top: 5px;
    width: auto;
    padding: 0 15px;

    @include lg {
      padding-left: 0;
    }

    span {
      font-style: italic;
      color: #666666;
    }
  }

  img {
    width: 100%;
  }

  &--info {
    display: flex;
    align-items: center;
    padding: 20px 15px;
    background: #f0f0f0;

    @include sm {
      order: 2;
      padding: 20px 38px;
    }
  }
  &--pic {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
