// В этом файле должны быть стили для БЭМ-блока adaptation, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.adaptation {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  padding: 50px 0;
  background-color: $color-secondary;
  overflow: hidden;

  @include lg {
    min-height: 700px;
    padding: 110px 0;
  }

  &::after {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    z-index: 2;
  }

  .container {
    position: relative;
    z-index: 3;
  }

  .tabs-bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    display: none;

    @include md {
      display: block;
      opacity: 1;
    }
  }
}

.adaptation__bg {
  opacity: 0;
  transition: all 0.4s;
  display: none;
  height: 100%;

  &--active {
    display: block;
    opacity: 1;
    animation: myfadein ease .4s;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.adaptation__title {
  margin-bottom: 30px;
  font-size: 28px;
  color: $white;

  @include sm {
    font-size: 36px;
  }
}

.adaptation__btns {
  text-align: center;

  @include sm {
    margin-top: 40px;
  }

  @include lg {
    margin-top: 100px;
  }
}
