// В этом файле должны быть стили для БЭМ-блока panoramas, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.panoramas {

  $block-name:                &; // #{$block-name}__element

  background-color: $color-secondary;
  padding: 40px 0;

  @include lg {
    padding: 100px 0;
  }
}

.panoramas__title {
  margin-bottom: 45px;
  color: $white;
  text-align: center;
}

.panoramas__inner {

  
  @include sm {
    display: flex;
    justify-content: center;
  }
}

.panoramas__item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 80vw;
  height: 80vw;
  text-decoration: none;
  border-radius: 50%;
  overflow: hidden;
  
  @include sm {
    width: 45vw;
    height: 45vw;
    margin: 0;
  }
  @include lg {
    width: 50%;
    height: 465px;
  }
  @include xl {
    width: 490px;
    height: 490px;
  }
  @include rwd(1440) {
    width: 570px;
    height: 570px;
  }

  &:hover {
    text-decoration: none;

    &:after {
      transform: translate(-50%,-50%) scale(.9) rotate3d(1, -1, 1, 75deg);
      opacity: 1;
    }

    .panoramas__name {

      &:before {
        transform: scale(1);
      }
    }
  }

  &:after {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    border: 2px solid $white;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    opacity: 0;
    transform: translate(-50%,-50%) scale(0) rotate3d(1, -1, 1, 75deg);
    transition: transform .3s cubic-bezier(0, 0.21, 0.08, 0.68);
    z-index: 2;

    @include xl {
      transition: transform .4s cubic-bezier(0, 0.21, 0.08, 0.68) .1s;
    }
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
  }
}

.panoramas__name {
  position: relative;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 110px;
  height: 110px;
  border-radius: 50%;
  color: $white;
  font-size: 18px;
  font-family: $font-title;
  background: $black;

  @include sm {
    
  }
  @include lg {
    width: 150px;
    height: 150px;
    font-size: 24px;
  }
  @include xl {
    width: 200px;
    height: 200px;
    background: none;
  }

  &:before {

    @include xl {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      background: $black;
      position: absolute;
      top: 0;
      left: 0;
      transform: scale(0);
      transition: all .2s;
      z-index: -1;
      border-radius: 50%;
    }
  }
}