// В этом файле должны быть стили для БЭМ-блока social, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.social {

  $block-name:                &; // #{$block-name}__

  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;

  &__link-wrap {
    margin-right: 10px;
  }

  &__link {
    display: block;
    padding: 5px;
    background: none;
    transition: all .4s;

    &:hover {
      background: $black;

      svg {
        fill: $white;
      }
    }

    // #{$block-name}__link-wrap--fb & {
    //   background-color: $color-fb;
    // }
    // #{$block-name}__link-wrap--ytb & {
    //   background-color: $color-ytb;
    // }
    // #{$block-name}__link-wrap--inst & {
    //   background: $bg-color-inst;
    // }

    // #{$block-name}__link-wrap--vk & {
    //   background-color: $color-vk;
    // }

    // #{$block-name}__link-wrap--tw & {
    //   background-color: $color-tw;
    // }

    // #{$block-name}__link-wrap--gh & {
    //   background-color: $color-gh;
    // }

    svg {
      display: block;
      width: 20px;
      height: 20px;
      fill: $black;
      transition: all .4s;
    }
  }

  &__text {
    @include hide-text;
  }
}
