// В этом файле должны быть стили для БЭМ-блока about, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.about {

  $block-name:                &; // #{$block-name}__element

  margin-bottom: 40px;

  @include sm {
    display: flex;
    flex-wrap: wrap;
  }
  @include md {
    margin-bottom: 75px;
    padding-top: 130px;
  }
  @include xl {
    padding-top: 180px;
  }

  .container {
    padding: 0;
  }
}

.about__item {
  margin: auto;

  @include sm {
    max-width: initial;
    width: 50%;
    flex-basis: 50%;
    margin: 0;
  }

  &--info {
    
    @include sm {
      display: flex;
      justify-content: flex-end;
    }
  }

  &--pic {
    max-width: 430px;
    margin-top: 30px;

    @include sm {
      max-width: initial;
    }
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.about__container {
  max-width: 600px;
  padding-top: 30px;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 20px;

  @include md {
    padding-top: 0;
    padding-right: 30px;
  }
  @include lg {
    padding-right: 60px;
  }
  @include xl {
    padding-right: 120px;
  }
}

.about__info {
  margin-bottom: 50px;

  @include md {
    margin-bottom: 120px;
  }

  &:last-child {
    margin-bottom: 0;

    @include sm {
      margin-bottom: 30px;
    }
  }
}

.about__title {
  margin-bottom: 20px;
  font-size: 36px;  

  @include md {
    font-size: 36px;
  }

  @include lg {
    margin-bottom: 35px;
  }

  @include xl {
    font-size: 72px;
  }
}

.about__text {
  font-size: 16px;

  @include md {
    font-size: 18px;
  }
  @include lg {
    font-size: 22px;
  }
}

.about__pic {

  img {
    width: 100%;
  }
}