// В этом файле должны быть стили для БЭМ-блока callback, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.callback {

  $block-name:                &; // #{$block-name}__element

  padding: 50px 0;
  background-color: $color-main;
  // background-image: linear-gradient(45deg, #c2d402, #E29936);
}

.callback__inner {
  max-width: 870px;
  width: 100%;
  margin: auto;
  padding: 35px 15px;
  background: $white;

  @include md {
    padding: 55px;
  }

  @include lg {
    padding: 55px 85px;
  }
}

.callback__title {
  margin-bottom: 35px;
  font-size: 25px;
  text-align: center;

  @include md {
    font-size: 36px;
  }
}

.callback__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  max-width: 320px;
  margin: auto;

  @include sm {
    max-width: initial;
    flex-direction: row;
    align-items: flex-end;
  }

  .field-text {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    width: 100%;

    @include sm {
      width: calc(50% - 68px);
    }

    @include md {
      width: auto;
    }

    & + .field-text {

      @include sm {
        margin-left: -1px;
      }
    }

    &__input {
      min-width: 175px;
      padding: 13px 12px;
      border-radius: 0;
      border: 1px solid $black;
    }

    &__name {
      align-self: flex-start;
      margin: 0;
      padding: 0 3px;
      background: $black;
      color: $white;
      font-size: 12px;
      line-height: 1.2em;
      font-weight: 300;
    }
  }

  .field-actions {
    flex-shrink: 0;

    @include sm {
      margin-left: 15px;
      width: 120px;
    }

    @include md {
      width: auto;
    }

    .btn {
      padding: 17px 40px;
      font-size: 13px;
      font-weight: bold;
      text-transform: uppercase;
      background: $black;
      color: $white;

      @include sm {
        padding: 17px 20px;
      }
      @include md {
        padding: 17px 40px;
      }

      &:hover {
        
      }
    }
  }

  .field-checkbox {

    @include sm {
      width: 100%;
      order: 5;
    }
  }
}

