// В этом файле должны быть стили для БЭМ-блока deal-item, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.deal-item {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  padding: 25px 15px;
  border-top: 1px  solid $black;

  @include md {
    max-width: 31%;
    padding: 0;
    padding-bottom: 100px;
    border: none;
  }
  @include xl {
    max-width: 360px;
    padding-bottom: 145px;
  }

  .btn {
    margin-top: 25px;
    padding: 17px 40px;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 13px;
    border: 1px solid $color-main;

    @include md {
      margin-top: 40px;
    }
  }
}

.deal-item__title {
  margin-bottom: 12px;
  font-size: 30px;
  font-family: $font-title;
}

.deal-item__text {
  font-size: 16px;
}

.deal-item__number {
  display: none;

  @include md {
    display: block;
    width: 200px;
    position: absolute;
    bottom: -60px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 140px;
    font-weight: 500;
    line-height: 1;
    color: $white;
    text-shadow: 0 1px 4px $black;
    transition: all .6s;
    cursor: default;
    text-align: center;
  }
  @include xl {
    width: 282px;
    font-size: 230px;
    bottom: -100px;
  }

  &--active {

    @include md {
      color: $color-main;
      text-shadow: none;
    }
  }
}