.main-nav {
  display: none;

  @include xl {
    display: block;
    width: auto;
    margin-left: auto;
    padding: 0;
    transform: none;
    background: none;
    transition: none;
  }

  &--mobile {
    display: block;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    width: 270px;
    padding: 20px;
    padding-bottom: 30px;
    padding-left: 30px;
    padding-top: 75px;
    background: $color-secondary;
    transition: all .4s;
    transform: translateX(110%);
    z-index: 9999;

    @include xl {
      display: none;
    }
  }

  &--open {
    transform: translateX(0);
  }
}

.main-nav__list {
  display: flex;
  flex-direction: column;

  @include xl {
    flex-direction: row;
  }
}

.main-nav__item {
  margin: 0 10px;
  margin-bottom: 15px;
  padding: 5px 10px;
  text-decoration: none;
  color: #e5e5e5;
  font-size: 18px;
  line-height: 1.2em;
  transition: all .2s;
  font-weight: 700;

  @include xl {
    margin: 0;
    margin-right: 20px;
    color: $white;
    font-size: 14px;
    text-transform: uppercase;

    &:last-child {
      margin-right: 0;
    }
  }

  &:hover {
    text-decoration: none;
    color: $black;
    background: $white;
  }
}

.main-nav__close {
  position: absolute;
  top: 10px;
  right: 10px;
  width:50px;
  height:50px;
  margin: 0;
  border-radius:5px;
  cursor: pointer;

  @include xl {
    display: none;
  }

  &:hover {
    background:rgba(255,255,255,.4);
  }
}

.main-nav__close-inner {
  position:absolute;
  height:100%;
  width:100%;
  transform:rotate(45deg);

  span{
    display:block;
    background:#fff;
    border-radius:2px;

    &:nth-child(1){
      height:80%;
      width:4px;
      position:absolute;
      top:10%;
      left:24px;
      transition-delay:0s;
    }
    &:nth-child(2){
      width:80%;
      height:4px;
      position:absolute;
      left:10%;
      top:24px;
      transition-delay:.25s;
    }
  }
}

.nav-overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,.7);
  z-index: 9998;

  &--open {
    display: block;
    animation: myfadein ease .4s;

    @include xl {
      display: none;
    }
  }
}