// В этом файле должны быть стили для БЭМ-блока tech, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.tech {

  $block-name:                &; // #{$block-name}__element

  margin-bottom: 40px;

  @include lg {
    margin-bottom: 105px;
  }
}

.tech__title {
  margin-bottom: 20px;
  padding: 0 15px;
  font-size: 36px; 

  @include md {
    width: 50%;
    font-size: 36px;
    text-align: right;
  }
  @include lg {
    margin-bottom: 35px;
  }
  @include xl {
    font-size: 72px;
  }
}

.tech__container {
  margin-bottom: 40px;

  @include sm {
    display: flex;
    flex-direction: row-reverse;
  }
  @include md {
    margin-bottom: 40px;
  }
  @include xl {
    margin-bottom: 100px;
  }
}

.tech__item {
  margin: auto;

  @include sm {
    max-width: initial;
    width: 50%;
    flex-basis: 50%;
    margin: 0;
  }
}

.tech__inner {
  max-width: 540px;
  margin-bottom: 25px;
  padding-left: 15px;
  padding-right: 15px;

  @include sm {
    margin-bottom: 0;
  }
  @include md {
    padding-top: 0;
    padding-left: 30px;
  }
  @include lg {
    padding-left: 60px;
  }
}

.tech__description {
  max-width: 365px;
  margin-bottom: 25px;
  font-size: 16px;

  @include md {
    margin-bottom: 65px;
  }
}

.tech__text {
  font-size: 18px;
  font-family: $font-title;

  @include sm {
    font-size: 16px;
  }
  @include md {
    font-size: 18px;
  }
  @include lg {
    font-size: 22px;
  }
  @include xl {
    font-size: 30px;
  }
}

.tech__pic {
  max-width: 430px;
  margin: auto;

  @include sm {
    max-width: initial;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.tech__eco {
  max-width: 700px;
  margin: auto;
  margin-bottom: 40px;
  padding: 0 15px;
  
  @include sm {
    display: flex;
    align-items: center;
  }
  @include lg {
    margin-bottom: 90px;
  }
  @include xl {
    max-width: 880px;
  }

  .tech__pic {
    flex-shrink: 0;
    width: 110px;
    margin: 0;
    margin-bottom: 20px;
    margin-right: 20px;

    @include md {
      margin: 0;
      margin-right: 35px;
    }
    @include xl {
      width: 160px;
      margin-right: 55px;
    }

    img {
      width: 100%;
      height: auto;
      object-fit: initial;
    }
  }
}

.tech__fullpic {

  img {
    width: 100%;
  }
}