// В этом файле должны быть стили для БЭМ-блока equipment, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.equipment {

  $block-name:                &; // #{$block-name}__element

  padding-top: 70px;
  padding-bottom: 2px;
}

.equipment__title {
  margin-bottom: 65px;
  text-align: center;
  font-size: 36px;
}

.equipment__list {
  position: relative;
  background: #f0f0f0;

  &::before {

    @include lg {
      content: '';
      display: block;
      height: 100%;
      width: 2px;
      background: $white;
      position: absolute;
      top: 0;
      left: 50%;
    }
  }
  
  .container {

    @include lg {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
  }
}

.equipment__item {
  display: flex;
  flex-direction: column;
  padding: 25px 0;

  @include sm {
    padding: 25px 50px;
  }

  @include lg {
    padding: 50px;
    padding-top: 40px;
    padding-left: 0;
  }

  & + .equipment__item {

    @include lg {
      padding-left: 50px;
      padding-right: 0;
    }
  }
}

.equipment__name {
  margin-bottom: 30px;
  font-size: 30px;
  line-height: 1.2em;
  text-decoration: underline;
}

.equipment__info {
  font-size: 15px;
  display: flex;
  flex-direction: column;

  @include sm {
    flex-direction: row;
    flex-grow: 1;
    flex-wrap: wrap;
  }
}

.equipment__pic {
  max-width: 340px;
  width: 100%;
  margin: auto;
  margin-bottom: 20px;
  order: 5;

  @include sm {
    order: initial;
    width: 76px;
    margin: 0;
    flex-shrink: 0;
  }

  img {
    width: 100%;
  }
}

.equipment__text {
  
  @include sm {
    width: calc(100% - 96px);
    margin-right: 20px;
  }
  @include lg {
    width: calc(100% - 146px);
    margin-right: 70px;
  }
}

.equipment__options {
  display: flex;
  flex-wrap: wrap;
  margin-top: 15px;
  margin-bottom: 15px;
  padding-top: 16px;
  border-top: 1px solid $black;
}

.equipment__option {
  margin-right: 30px;
  margin-bottom: 20px;
  font-size: 10px;
  text-transform: uppercase;
  font-weight: 700;
}