// В этом файле должны быть стили для БЭМ-блока book, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.book {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  
  @include md {
    background: $color-secondary;
  }

  .book__inner {
    position: relative;
    z-index: 2;
  }
  .container {
    padding: 0;
    
    @include sm {
      display: flex;
      flex-wrap: wrap;
    }
    @include md {
      display: block;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
}

.book__bg {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  @include md {
    display: block;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }
}

.book__info {
  max-width: 430px;
  margin: auto;
  background-color: $white;

  @include sm {
    width: 50%;
    margin: initial;
    flex-shrink: 0;
  }
  @include md {
    max-width: 300px;
  }

  &--top {
    position: relative;
    padding: 40px 30px;
    padding-bottom: 110px;
    
    overflow: hidden;
    background-image: url('../img/arkhiz.jpg');
    background-repeat: no-repeat;
    background-position: 15px 100%;
    background-size: 90%;

    @include md {
      margin-bottom: 60px;
      background-position: 0 100%;
      background-size: 100%;
    }

    @include lg {
      margin-bottom: 0;
      transform: translateY(-70px);
    }

    &::after {
      content: '';
      display: block;
      width: 100%;
    }
    .book__text {
      padding: 0;
    }
  }
}

.book__title {
  margin-bottom: 20px;
  font-size: 36px;

}

.book__text {
  padding: 40px 30px;
  font-size: 14px;

  p {
    margin-bottom: 25px;
    font-size: 14px;
  }
}

.book__pic {
  margin-bottom: 0;
  
  @include md {
    border: 3px solid $white;
  }

  &--mobile {

    @include sm {
      display: none;
    }
  }

  img {
    width: 100%;
  }
}

.book__bottom {
  max-width: 430px;
  margin: auto;
  margin-top: 5px;
  
  @include sm {
    max-width: initial;
    display: flex;
    flex-direction: row-reverse;
  }

  @include md {
    max-width: 600px;
    margin-top: 35px;
    transform: translateY(60px);
  }

  .book__pic {

    @include sm {
      width: 50%;
      flex-shrink: 0;
    }
    
  }
  .book__text {
    display: flex;
    align-items: center;
    padding: 40px 30px;
    background: $white;
  }
}

.book__order {
  
  @include md {
    max-width: 340px;
    margin: 0;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
  }

  @include lg {
    max-width: 516px;
  }

  .book__title {
    font-size: 28px;

    @include lg {
      font-size: 36px;
    }
  }
}

.book__contact {
  padding: 45px 15px;
  padding-bottom: 20px;
  background: $color-main;

  @include sm {
    padding: 45px;
  }

  p {
    margin-bottom: 28px;
    font-size: 18px;
  }
}

