.book__form {
  padding: 45px 15px;
  padding-top: 30px;
  background: $color-main;

  @include md {
    background: $white;
    padding: 45px;
    padding-top: 30px;
  }

  .policy-link {
    color: $black;
    text-decoration: underline;

    @include md {
      text-decoration: none;
      color: $color-main;
    }
  }

  .callback__form {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    max-width: 340px;
    margin: auto;

    @include sm {
      max-width: 500px;
      flex-direction: row;
    }
    
    @include md {
      display: block;
    }

    .field-actions {
      margin-left: 0;
      width: auto;

      @include md {
        width: 100%;
      }
    }
    .field-text {
      width: 100%;

      @include sm {
        width: calc(50% - 78px);
      }
      @include md {
        width: 100%;
      }
    }
  }
  
  .btn {
    display: block;
    width: 100%;
  }
}

