// В этом файле должны быть стили для БЭМ-блока btn, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.btn {
  box-sizing: border-box;
  display: inline-block;
  max-width: 100%;
  margin: 0;
  border: 0;
  padding: $field-padding-vertical  $field-padding-horizontal;
  vertical-align: middle;
  white-space: nowrap;
  text-overflow: ellipsis;
  user-select: none;
  text-align: center;
  font-size: 1em;
  font-family: inherit;
  line-height: $line-height;
  text-decoration: none;
  text-transform: none;
  color: $text-color;
  background-color: $gray-lightest;
  background-image: none;
  cursor: pointer;
  overflow: hidden;
  transition: all .2s;

  &:hover,
  &:focus {
    text-decoration: none;
    color: $text-color;
    background-color: darken($gray-lightest, 10%);
    // border-color: darken($gray-lighter, 12%);
  }

  // &:visited {
  //   color: $text-color;
  // }

  &:active,
  &:focus {
    @include field-focus;
  }

  &--disabled,
  &:disabled {
    opacity: $opacity;
    cursor: not-allowed;
    color: $text-color;
    background-color: $gray-lightest;
    // border-color: $gray-lighter;

    @at-root a.btn--disabled {
      pointer-events: none;
    }
  }

  &--yellow {
    background: $color-main;
    color: $black;

    &:hover,
    &:focus {
      background-color: $white;
      // border-color: darken($gray-lighter, 12%);
    }
  }

  &--doc {
    position: relative;
    padding: 15px;
    padding-left: 41px;
    font-size: 18px;
    line-height: 1em;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-weight: bold;
    overflow: hidden;

    &:hover {

      &::before {
        animation: iconjump 1s ease-in-out;
      }
    }

    &::before {
      content: '';
      display: block;
      width: 26px;
      height: 26px;
      background-image: url("data:image/svg+xml,%3Csvg fill='%23000000' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 32 32' width='128px' height='128px'%3E%3Cpath d='M 6 3 L 6 29 L 26 29 L 26 9.59375 L 25.71875 9.28125 L 19.71875 3.28125 L 19.40625 3 Z M 8 5 L 18 5 L 18 11 L 24 11 L 24 27 L 8 27 Z M 20 6.4375 L 22.5625 9 L 20 9 Z M 11 13 L 11 15 L 21 15 L 21 13 Z M 11 17 L 11 19 L 21 19 L 21 17 Z M 11 21 L 11 23 L 21 23 L 21 21 Z'/%3E%3C/svg%3E");
      background-repeat: no-repeat;
      background-size: contain;
      position: absolute;
      left: 10px;
      top: 10px;
    }
  }

  &--moreoptions {
    padding: 15px 32px;
    background: $white;
    color: $black;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 700;
    text-align: center;

    &:hover {
      background: $color-main;
      
    }
  }


