// В этом файле должны быть стили для БЭМ-блока promo-description, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.promo-description {

  $block-name:                &; // #{$block-name}__element

  overflow: hidden;
  
  @include sm {
    display: flex;
    flex-direction: row-reverse;
  }

}

.promo-description__pic {
  max-width: 430px;
  margin: auto;

  @include sm {
    max-width: initial;
    width: 50%;
    flex-basis: 50%;
    margin: 0;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.promo-description__info {
  margin-bottom: 30px;
  
  @include sm {
    width: 50%;
    flex-basis: 50%;
    margin-bottom: 0;
  }
}

.promo-description__info-inner {
  max-width: 600px;
  padding-top: 30px;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 20px;

  @include md {
    padding-left: 30px;
  }
  @include lg {
    padding-top: 50px;
    padding-left: 60px;
  }
  @include xl {
    padding-top: 100px;
    padding-left: 120px;
  }
}

.promo-description__title {
  margin-bottom: 20px;
  font-size: 36px;  

  @include md {
    font-size: 36px;
  }

  @include lg {
    margin-bottom: 35px;
  }

  @include xl {
    font-size: 72px;
  }
}

.promo-description__text {
  font-size: 16px;

  @include md {
    font-size: 18px;
  }
  @include lg {
    font-size: 22px;
  }
}