// В этом файле должны быть стили для БЭМ-блока inside, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.inside {

  $block-name:                &; // #{$block-name}__element

  margin-bottom: 50px;
  overflow: hidden;

  @include lg {
    margin-bottom: 100px;
  }
  .container {
    max-width: 1200px;
    margin-bottom: 25px;
    
    @include lg {
      display: flex;
      align-items: flex-end;
      margin-bottom: 50px;
    }
  }
}

.inside__title {
  margin-bottom: 20px;
  font-size: 36px; 

  @include md {
    
  }
  @include lg {
    max-width: 280px;
    margin: 0;
    margin-right: 40px;
    font-size: 46px;
    flex-shrink: 0;   
  }
  @include xl {
    max-width: 430px;
    margin-right: 90px;
    font-size: 72px;
  }
}

.inside__text {
  font-size: 20px;

  @include md {
    font-size: 18px;
  }
  @include lg {
    margin-bottom: 15px;
    line-height: 1.5em;
  }
}

.inside__pics {
  margin-bottom: 25px;

  @include sm {
    display: flex;
    align-items: flex-end;
  }
  @include md {
    margin-bottom: 0;
  }
}

.inside__pic {

  img {
    width: 100%;
  }

  &--big {
    max-width: 430px;
    margin: auto;
    flex-grow: 1;

    @include sm {
      max-width: initial;
      margin: 0;
    }
  }
  &--small {
    max-width: 330px;
    margin: auto;
    flex-shrink: 0;
    border-radius: 50%;
    overflow: hidden;

    @include sm {
      max-width: initial;
      width: 33%;
      margin: 0;
    }
    @include lg {
      width: 25%;
      max-width: 400px;
    }
  }
}

.inside__container {
  display: flex;
  flex-direction: column;

  @include md {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
    gap: 40px;
  }
  @include lg {
    grid-gap: 50px 65px;
    gap: 50px 65px;
  }

  .inside__text {
    padding: 0 15px;
    padding-top: 20px;
  }
}

.inside__item {

  &:first-child {
    
    @include md {
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
    }

    .inside__text {
      margin-bottom: 25px;

      @include md {
        max-width: 480px;
        margin-bottom: 0;
      }
      @include rwd(1440) {
        margin-right: 50px;
      }
    }
  }
  &:last-child {
    order: 2;

    @include md {
      order: 0;
    }

    .inside__text {

      @include md {
        max-width: 330px;
        padding-top: 0;
      }
    }
  }
  &--pics {
    order: 1;

    @include sm {
      display: flex;
    }
    @include md {
      order: 0;
    }

    .inside__img {
      max-width: 430px;
      margin: auto;

      @include sm {
        max-width: initial;
        width: 50%;
      }
    }
  }
}

.inside__img {

  img {
    width: 100%;
  }
}