// В этом файле должны быть стили для БЭМ-блока feature-item, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.feature-item {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  display: flex;
  justify-content: center;
  padding: 30px 5px;
  padding-top: 35px;
  color: $white;
  background: $color-secondary;
  overflow: hidden;

  @include md {
    padding: 30px;
    padding-top: 35px;
    justify-content: flex-start;
  }

  @include lg {
    
    &:hover {

      .feature-item__icon {
        transform: scale(0);
        width: 0;
        margin: 0;
      }
      .feature-item__bg {
        opacity: 1;
      }
    }
  }

}

.feature-item__bg {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all .4s;

  @include lg {
    display: block;
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    filter: brightness(0.6);
  }
}

.feature-item__icon {
  width: 75px;  
  margin-right: 15px;
  transition: all .4s;
  overflow: hidden;

  @include md {
    margin-right: 30px;
  }

  svg {
    width: 100%;
    height: auto;
    fill: $color-main;
  }
}

.feature-item__info {
  position: relative;
  max-width: 340px;
  z-index: 2;
}

.feature-item__title {
  margin-bottom: 15px;
  font-size: 24px;

  @include md {
    font-size: 30px;
  }
}

.feature-item__text {
  font-size: 14px;
  line-height: 1.4em;
  color: #e5e5e5;

  @include md {
    font-size: 16px;
  }
}
