// Стилизация БЭМ-блока page.
// Общие стили тегов.
// Подключение шрифтов.
// Типографика.


// Сделаем viewport великим снова! (привет, IE10) http://timkadlec.com/2012/10/ie10-snap-mode-and-responsive-design/
@at-root {

  @-ms-viewport {
    width: device-width;
  }
}


/* stylelint-disable */
@font-face {
  font-family: 'Geometria';
  font-weight: normal;
  font-style: normal;
  src: local('Geometria'),
       url('../fonts/Geometria-Medium.woff') format('woff'),
       url('../fonts/Geometria-Medium.woff2') format('woff2');
  font-display: swap;
}
@font-face {
  font-family: 'MuseoSansCyrl';
  font-style: normal;
  font-weight: 300;
  src: local('MuseoSansCyrl'),
       url('../fonts/MuseoSansCyrl-300.woff') format('woff'),
       url('../fonts/MuseoSansCyrl-300.woff2') format('woff2');
  font-display: swap;
}
@font-face {
  font-family: 'MuseoSansCyrl';
  font-style: normal;
  font-weight: 500;
  src: local('MuseoSansCyrl'),
       url('../fonts/MuseoSansCyrl-500.woff') format('woff'),
       url('../fonts/MuseoSansCyrl-500.woff2') format('woff2');
  font-display: swap;
}
@font-face {
  font-family: 'MuseoSansCyrl';
  font-style: normal;
  font-weight: 700;
  src: local('MuseoSansCyrl'),
       url('../fonts/MuseoSansCyrl-700.woff') format('woff'),
       url('../fonts/MuseoSansCyrl-700.woff2') format('woff2');
  font-display: swap;
}
/* stylelint-enable */
@keyframes fadeinblock {
  0% {
      display: block;
      opacity: 0;
  }

  100% {
      opacity: 1;
  }
}
// animation: fadeinblock ease .4s;

@keyframes myfadein {
  0% {
      opacity: 0;
  }

  100% {
      opacity: 1;
  }
}
// animation: myfadein ease .4s;


@keyframes slideInDown {
  from {
      transform: translate3d(0,-100%,0);
      visibility: visible;
  }

  to {
      transform: translate3d(0,0,0);
  }
}
// animation: slideInDown .6s both;

@keyframes iconjump {
  0%,to {
      transform: translateY(0);
  }

  20%,70% {
      transform: translateY(3px);
  }

  50% {
      transform: translateY(-1px);
  }
}
// animation: iconjump 1s ease-in-out;

@keyframes iconscrollup {
	0% {
		transform: translateY(0);
	}
	60% {
		transform: translateY(-110%);
	}
	65% {
		opacity: 0;
		transform: translateY(110%);
	}
	67% {
		opacity: 1;
		transform: translateY(100%);
	}
	100% {
		transform: translateY(0);
	}
}
// animation: iconscrollup .4s cubic-bezier(0.83, 0.23, 0.48, 0.99) forwards;

@keyframes shadowpulse {
  0% { box-shadow: 0 0 0 0 $color-main; }
}
// animation: shadowpulse 1s;
// box-shadow: 0 0 0 2em rgba(#fff,0);

.page {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: 100%;
  min-width: 320px;
  min-height: 100%;

  &__inner {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }

  &__content {
    flex-grow: 1;
  }

  &__footer-wrapper {
    flex-shrink: 0;
  }
}

*, *:before, *:after { box-sizing: border-box; }

* {
  -webkit-tap-highlight-color: transparent;
}

html {
  line-height: $line-height;
  font-size: $font-size;
  font-weight: $font-weight;
	color: $main-text-color;
  height: 100%;
  @include md {
    font-size: 15px;
  }
  @include xl {
    font-size: 16px;
  }
}

body {
  display: flex;           // Исправляем баг в IE для min-height and flexbox (flex-direction:column)
  flex-direction: column;  // и прижимаем footer в IE 10-11
  margin: 0;
  min-height: 100%;
  word-break: break-word;
  font-family: $font-family;
  // font-size: $font-size;
  font-size: 20px;
  line-height: $line-height;
  font-weight: $font-weight;
  text-align: left;
  color: $main-text-color;
  background-color: $body-bg;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  @include md {
    font-size: 18px;
  }
}

[tabindex='-1']:focus {
  outline: none !important;
}

// Коррекция для кликабельных элементов с указанной ролью в iOS
[role='button'] {
  cursor: pointer;
}

// Мы любим Сафари (на самом деле, нет).
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

// area,
// summary,
a,
button,
[role='button'],
input,
label,
select,
textarea {
  touch-action: manipulation; // Стилевая коррекция 300ms задержки (для поддерживающих браузеров)
  margin: 0;                  // Убираем margin в Firefox и Safari
}

button,
input {
  overflow: visible;    // Покажем торчащее в Edge
  text-transform: none; // Уберём наследование text transform в Firefox
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; // Коррекция проблем стилизации для Safari
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox; // Коррекция для Mobile Safari (https://github.com/twbs/bootstrap/issues/11266)
}

textarea {
  overflow: auto;   // Удаление верт. скролла в IE.
  resize: vertical; // Правильное изменение размеров
}

svg {
  fill: currentColor;

  a &,
  button & {
    pointer-events: none; // Мы любим Edge, это браузер! (почти)
  }

  &:not(:root) {
    overflow: hidden; // Скрыть выступающее за границы в IE
  }
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: $link-color;
  background-color: transparent;
  transition: color 0.3s;

  &:hover,
  &:focus {
    // color: $link-color--hover;
  }

  // &:visited {
  //   color: $link-color--hover;
  // }

  &[href^="tel:"] {

    // На больших экранах телефоны-ссылки делаем обычным текстом
    @media (min-width: $screen-xl) {
      white-space: nowrap;
      pointer-events: none;
      text-decoration: inherit;
      color: inherit;
    }
  }
}

hr {
  box-sizing: content-box; // Проблема Firefox
  height: 0;               // Проблема Firefox
  overflow: visible;       // Проблема Edge и IE
  margin-top: $typo-margin-vertical * 2;
  margin-bottom: $typo-margin-vertical * 2;
  border: 0;
  border-top: 1px solid $border-color;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-family: $font-title;
  font-weight: 500;
  line-height: 1.2;
  color: currentColor;
}

h1 { font-size: $font-size-h1; }
h2 { font-size: $font-size-h2; }
h3 { font-size: $font-size-h3; }
h4 { font-size: $font-size-h4; }
h5 { font-size: $font-size-h5; }
h6 { font-size: $font-size-h6; }

p,
ul,
ol,
dl,
table,
blockquote,
pre,
address {
  margin-top: 0;
  margin-bottom: $typo-margin-vertical;
}

figure {
  margin: 0;
}

p {
  // http://clagnut.com/blog/2395
  hyphens: auto;
  hyphenate-limit-chars: 6 3 3;
  hyphenate-limit-lines: 2;
  hyphenate-limit-last: always;
  hyphenate-limit-zone: 8%;
}

address {
  font-style: normal;
}

ul,
ol {
  margin-bottom: $typo-margin-vertical;
  padding-left: 1.5em;

  ul,
  ol {
    margin-top: 0;
    margin-bottom: 0;
  }
}

li {
  line-height: inherit;
}

