b,
strong {
  font-weight: bolder;
}

// abbr[title] {
//   text-decoration: underline dotted;
// }

blockquote {
  padding: 0;
  margin-left: 0;
  margin-right: 0;
}

// samp,
code,
kbd,
pre {
  font-family: $font-family--monospace;
}

code {
  padding: 0.06em 0.3em;
  color: $text-color;
  background-color: rgba(#000, 0.08);
  border-radius: $border-radius;
}

kbd {
  padding: 0.06em 0.3em;
  color: $text-color;
  background-color: $gray-lightest;
  border-radius: $border-radius;

  kbd {
    padding: 0;
  }
}

pre {
  display: block;
  width: 100%;
  margin-bottom: $typo-margin-vertical;
  overflow-x: auto;
  tab-size: 2;
  background-color: rgba(#000, 0.08);

  code {
    background: transparent;
  }
}

.slick-slider {

  .slick-track {
    transform: translate3d(0, 0, 0);
    transition-delay: 10ms;
  }
  .slick-list {
    transform: translate3d(0, 0, 0);
    transition-delay: 10ms;
  }
}

.slick-slide {
  will-change: transform;
}

.slick-prev {
  z-index: 2;

  &::before {
    display: none;
  }
}

.slick-next {
  z-index: 2;

  &::before {
    display: none;
  }
}

.modal-open {
  height: 100vh;
  overflow: hidden;
}

.policy {
  margin-bottom: 15px;
  font-size: 14px;
  color: $black;
  text-decoration: underline;

  @include md {
    margin-bottom: 0;
  }
}

.copy {
  font-size: 14px;
}