// В этом файле должны быть стили для БЭМ-блока page-footer, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page-footer {
  background: #d2d2d2;
}

.page-footer__top {
  display: flex;
  justify-content: space-between;
  padding: 15px 0;
  border-bottom: 1px solid $black;

  @include xl {
    padding: 25px 0;
  }
}

.page-footer__logo {
  width: 90px;

  svg {
    fill: $black;
  }
}

.page-footer__content {
  

  @include lg {
    display: flex;
    justify-content: space-between;

  }
}

.page-footer__content-left {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-top: 35px;
  padding-bottom: 45px;
  border-bottom: 1px solid $black;

  @include md {
    padding-top: 70px;
    padding-bottom: 80px;
  }
  @include xl {
    padding-top: 135px;
    padding-bottom: 145px;
  }

  .btn--doc {
    max-width: 280px;
    font-size: 14px;
    line-height: 1.2em;
    white-space: normal;
    text-align: left;
    background: none;
    border: 1px solid $black;

    &:hover {
      background: $color-main;
      border: 1px solid $color-main;
    }

    &::before {
      top: 15px;
      left: 6px;
      width: 30px;
      height: 30px;
    }
  }
}

.page-footer__content-right {
  display: none;

  @include lg {
    display: flex;
    align-items: center;
  }
}

.btn-top {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 180px;
  height: 180px;
  border-radius: 50%;
  border: 1px solid #8B8B8B;
  text-decoration: none;
  transition: all .2s;
  cursor: pointer;

  &:hover {
    box-shadow: 0 0 20px 10px #ddd;
  }

  svg {
    width: 30px;
    height: auto;
    margin-top: 10px;
  }
}

.page-footer__phone {
  margin-bottom: 30px;
  font-family: $font-title;
  font-size: 26px;
  color: $black;

  @include md {
    font-size: 36px;
  }
  @include xl {
    font-size: 54px;
  }
}

.page-footer__bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0;

  @include md {
    flex-direction: row-reverse;
    justify-content: space-between;
  }
}
