// В этом файле должны быть стили для БЭМ-блока promo, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.promo {

  $block-name:                &; // #{$block-name}__element

  position: relative;
  background-color: $color-secondary;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  @include md {
    padding-top: 90px;
  }
  @include lg {
    min-height: 100vh;
    padding-top: 120px;
  }

  .container {
    position: relative;
  }
}

.promo__inner {
  padding-bottom: 30px;

  @include md {
    max-width: 390px;
    padding-bottom: 100px;
  }
  @include lg {
    max-width: 430px;
    padding-bottom: 30px;
  }
  @include xl {
    max-width: 510px;
  }
  @include rwd(1440) {
    max-width: 640px;
  }
}

.promo__title {
  margin: 0;
  margin-bottom: 15px;
  color: $white;
  font-size: 30px;
  line-height: 1.2em;

  @include lg {
    margin-bottom: 20px;
    font-size: 36px;
  }
  @include xl {
    font-size: 42px;
  }
  @include rwd(1440) {
    margin-bottom: 25px;
    font-size: 54px;
  }
}

.promo__description {
  margin-bottom: 15px;
  font-size: 22px;
  line-height: 1.2em;
  color: $white;

  @include lg {
    margin-bottom: 20px;
    font-size: 24px;
  }
  @include xl {
    font-size: 28px;
  }
  @include rwd(1440) {
    margin-bottom: 35px;
    font-size: 36px;
  }
}

.promo__text {
  margin-bottom: 50px;
  font-size: 15px;
  line-height: 1.4em;
  color: $white;

  @include md {
    max-width: 340px;
    margin-bottom: 60px;
  }
  @include lg {
    max-width: initial;
  }
}

.promo__bg {
  display: none;
  opacity: 0;

  @include sm {
    display: block;
    opacity: 1;
    height: 370px;
    margin-bottom: 20px;
  }
  @include md {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    margin-bottom: 0;
  }

  &--mobile {
    display: block;
    opacity: 1;
    max-height: 500px;
    overflow: hidden;
    margin-bottom: 20px;

    @include sm {
      display: none;
      opacity: 0;
    }
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;

    @include sm {
      object-position: bottom;
    }
    @include md {
      object-position: center;
    }
  }
}