// В этом файле должны быть стили для БЭМ-блока tabs, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.tabs {

  $block-name:                &; // #{$block-name}__element

  display: none;
  opacity: 0;
  margin-top: $typo-margin-vertical;
  margin-bottom: $typo-margin-vertical;

  @include sm {
    display: block;
    opacity: 1;
  }

  &__links {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-end;
    margin: 0;
    padding: 0;
    list-style: none;

    @include xl {
      margin-bottom: 25px;
    }

    .no-js & {
      display: none;
    }
  }

  &__link-wrap {
    position: relative;
    margin-bottom: 25px;
    color: $text-color-muted;

    @include sm {
      width: calc(33.333333% - 20px);
    }

    @include xl {
      margin: 0;
      margin-right: 20px;
      min-width: 160px;
      width: auto;
      flex-grow: 1;
      flex-shrink: 0;
    }

    &:last-child {
      margin-right: 0;
    }

    &--active {
      color: $text-color;

      #{$block-name}__link {
        color: $black;
        background-color: $color-main;
        border: 1px solid $color-main;
      }
    }
  }

  &__link {
    display: block;
    padding: 12px 20px;
    color: $white;
    text-decoration: none;
    font-size: 16px;
    font-weight: 500;
    border: 1px solid #8b8b8b;
    transition: all .3s;

    &:hover,
    &:focus {
      color: $black;
      background-color: $color-main;
      text-decoration: none;
    }
  }

  &__content-wrapper {
    display: block;
    max-width: 580px;
    padding: $field-padding-vertical $field-padding-horizontal;

    @include md {
      border-top: none;
    }
  }

  &__content-item {
    min-height: 130px;
    color: $white;
    font-size: 16px;

    .js & {
      display: none;
    }

    & > *:last-child { // stylelint-disable-line  selector-combinator-blacklist
      margin-bottom: 0;
    }

    p {
      color: $white;
      font-size: 16px;
    }

    &--active {

      .js & {
        display: block;
      }
    }
  }
}
