// В этом файле должны быть стили для БЭМ-блока hamburger, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.hamburger {
  position: relative;
  display: block;
  width: 35px;
  height: 31px;
  margin: auto;
  margin-right: 0;
  background: none;
  border: none;
  outline: none;

  @include xl {
    display: none;
  }

  span {
    cursor: pointer;
    border-radius: 1px;
    height: 4px;
    width: 35px;
    background: $white;
    position: absolute;
    left: 0;
    top: 39%;
    display: block;

    &::before {
      cursor: pointer;
      border-radius: 1px;
      height: 4px;
      width: 35px;
      background: $white;
      position: absolute;
      top: -10px;
      display: block;
      content: '';
    }
    &::after {
      cursor: pointer;
      border-radius: 1px;
      height: 4px;
      width: 35px;
      background: $white;
      position: absolute;
      bottom: -10px;
      display: block;
      content: '';
    }
  }
}